IACnB,YAAY;IACZ;;sBAEkB;EACpB;AACF","sourcesContent":["@import url(\"https://fonts.googleapis.com/css2?family=Volkhov&display=swap\");\n@import url(\"https://fonts.googleapis.com/css2?family=Secular+One&display=swap\");\n\n@import url(\"https://fonts.googleapis.com/css2?family=Aleo&display=swap\");\n\n@import url(\"https://fonts.googleapis.com/css2?family=Assistant:wght@700&display=swap\");\n\n:root {\n  --base-color: #1d2d44;\n  --sub-base-color: #3e5c76;\n  --sub-color: #0d1321;\n  --text-color: #f0ebd8;\n  --base-p-fs: calc(0.8rem + 0.7vw);\n  --base-h1-fs: calc(2rem + 2vw);\n  --base-h2-fs: calc(1.4rem + 1.2vw);\n  --base-h3-fs: calc(1.2rem + 1vw);\n  --base-h1-ff: \"Secular One\", sans-serif;\n  --base-h2-ff: \"Assistant\", sans-serif;\n  --base-p-ff: \"Volkhov\", sans-serif;\n  --logo-width: max(35px, 30%);\n  --base-paragraph-ff: \"Nanum Myeongjo\", sans-serif;\n  --base-margin: 0.2em 0.6em;\n  --base-padding: 0.4em 0.6em;\n  --box-shadow: 5px 10px 30px black;\n}\n\n* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  background-color: var(--base-color);\n  height: 100vh;\n  display: grid;\n  align-items: center;\n  justify-items: center;\n}\n\n/* PAGE LOADER */\n.loader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: var(--base-color);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 100;\n  transition:\n    opacity 0.75s,\n    visibility 0.75s;\n}\n\n.loader-hidden {\n  opacity: 0;\n  visibility: hidden;\n}\n\n.loader::after {\n  content: \"\";\n  width: 75px;\n  height: 75px;\n  border: 15px solid var(--text-color);\n  border-top-color: var(--sub-base-color);\n  border-radius: 50%;\n  animation: loading 0.75s ease infinite;\n}\n\n@keyframes loading {\n  from {\n    transform: rotate(0turn);\n  }\n  to {\n    transform: rotate(1turn);\n  }\n}\n\n#general-weather-container {\n  height: 65%;\n  width: 70%;\n  display: grid;\n  grid-template-columns: 1.5fr 1fr;\n  background-color: var(--sub-base-color);\n  border-radius: 0.5em;\n}\n\n/* LEFT WEATHER INFO */\n\n.left-weather-info {\n  border: 3px solid var(--text-color);\n  border-radius: 0.5em;\n}\n\n.location-info {\n  display: flex;\n  justify-content: space-between;\n}\n\n.location-area {\n  line-height: 1;\n  padding: var(--base-padding);\n}\n\n.location-region-txt {\n  font-size: var(--base-h1-fs);\n  font-family: var(--base-h1-ff);\n  color: var(--text-color);\n}\n\n.location-area-txt {\n  font-size: var(--base-h2-fs);\n  font-family: var(--base-h2-ff);\n}\n\n.location-country-txt {\n  font-size: var(--base-h3-fs);\n  font-family: var(--base-h2-ff);\n}\n\n.location-time {\n  padding: var(--base-padding);\n  color: var(--text-color);\n}\n\n.location-time-txt {\n  font-family: var(--base-p-ff);\n  font-size: calc(0.7rem + 0.6vw);\n}\n\n.temp-container {\n  height: 10%;\n  display: grid;\n  justify-items: center;\n  margin-top: 7%;\n}\n\n.temperature-info {\n  width: 60%;\n  display: flex;\n  justify-content: center;\n  gap: 10px;\n}\n\n.temperature-info .temp-c,\n.temperature-info .temp-f {\n  border: 1px solid var(--text-color);\n  border-radius: 0.5em;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  justify-items: center;\n  align-items: center;\n  gap: 5px;\n  padding: var(--base-padding);\n}\n\n.celsius-icon,\n.f-icon {\n  width: max(20px, 15%);\n  filter: invert(88%) sepia(29%) saturate(142%) hue-rotate(350deg)\n    brightness(106%) contrast(88%);\n}\n\n.celsius-txt,\n.f-txt {\n  font-family: var(--base-p-ff);\n  font-size: var(--base-p-fs);\n}\n\n.location-weather-data {\n  height: 15%;\n  margin-top: 6%;\n  display: grid;\n  justify-items: center;\n}\n\n.location-weather {\n  width: 90%;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n}\n\n.weather-txt {\n  font-size: calc(1.6rem + 1.5vw);\n  font-family: var(--base-h2-ff);\n  color: var(--text-color);\n}\n\n.weather-icon-big {\n  width: max(5px, 13%);\n  justify-items: flex-start;\n  filter: invert(88%) sepia(29%) saturate(142%) hue-rotate(350deg)\n    brightness(106%) contrast(88%);\n}\n\n.location-prediction-container {\n  margin-top: 3%;\n  margin-bottom: 2%;\n  height: 25%;\n  width: 100%;\n  display: flex;\n  justify-content: space-around;\n}\n\n.prediction-container-1,\n.prediction-container-2,\n.prediction-container-3,\n.prediction-container-4 {\n  border: 1.5px solid var(--text-color);\n  border-radius: 0.5em;\n  width: 20%;\n  display: grid;\n  justify-items: center;\n  align-items: center;\n  text-align: center;\n}\n\n.prediction-time-1,\n.prediction-time-2,\n.prediction-time-3,\n.prediction-time-4 {\n  font-size: calc(1.1rem + 0.8vw);\n  font-family: \"Aleo\";\n  color: var(--text-color);\n}\n\n.prediction-location-1 {\n  font-size: calc(0.8rem + 0.7vw);\n  font-family: \"Aleo\";\n}\n\n.prediction-weather-1,\n.prediction-weather-2,\n.prediction-weather-3,\n.prediction-weather-4 {\n  font-size: calc(0.6rem + 0.5vw);\n  font-family: var(--base-h1-ff);\n  color: var(--text-color);\n}\n\n/* RIGHT WEATHER INFO */\n\n.weather-input-container {\n  /* border: 1px solid red; */\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 20px;\n  height: 7%;\n  padding: var(--base-padding);\n}\n\n#city-value {\n  height: 80%;\n  border-radius: 0.5em;\n  border: none;\n  outline: none;\n  appearance: none;\n  font-size: calc(0.5rem + 0.5vw);\n}\n\n#city-value:focus:invalid {\n  outline: 1px solid red;\n  background-color: pink;\n}\n\n.city-submit-btn {\n  padding: 0.4em 0.8em;\n  border-radius: 0.5em;\n  border: none;\n  background-color: var(--base-color);\n  color: var(--text-color);\n  cursor: pointer;\n}\n\n.city-submit-btn:hover {\n  scale: 1.1;\n}\n\n.error-messsage {\n  margin-top: 3%;\n  display: grid;\n  justify-items: center;\n  color: red;\n}\n\n.error-txt {\n  font-size: var(--base-p-fs);\n}\n\n.wind-humidity-container {\n  height: 10%;\n  margin-top: 8%;\n  display: flex;\n  justify-content: space-evenly;\n}\n\n.wind-container,\n.humidity-container {\n  width: 30%;\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  gap: 5px;\n}\n\n.weather-icon {\n  width: var(--logo-width);\n  filter: invert(88%) sepia(29%) saturate(142%) hue-rotate(350deg)\n    brightness(106%) contrast(88%);\n}\n\n.wind-info-txt,\n.humidity-info-txt {\n  font-family: var(--base-p-ff);\n  font-size: var(--base-p-fs);\n}\n\n.weather-specifics-container {\n  height: 30%;\n  margin-top: 8%;\n  display: grid;\n  grid-template-columns: repeat(2, minmax(100px, 1fr));\n  grid-template-rows: repeat(3, 1fr);\n  gap: 10px;\n  padding: var(--base-padding);\n}\n\n.weather-specifics {\n  border: 1px solid var(--text-color);\n  border-radius: 0.5em;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 4px;\n}\n\n.weather-specifics > p {\n  font-family: var(--base-p-ff);\n  font-size: calc(0.7rem + 0.6vw);\n}\n\n.sun-details-container {\n  padding: var(--base-padding);\n  margin-top: 5%;\n  height: 7%;\n  display: flex;\n  justify-content: center;\n  gap: 15px;\n}\n\n.sun-details-container > div {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-size: calc(0.7rem + 0.6vw);\n  font-family: var(--base-p-ff);\n  color: var(--text-color);\n}\n\n.sunrise-value,\n.sunset-value,\n.moon-phase-value {\n  font-size: calc(0.5rem + 0.5vw);\n  color: var(--sub-color);\n}\n\n.moon-phase-container {\n  margin-top: 5%;\n  padding: var(--base-padding);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 4px;\n  font-size: calc(0.7rem + 0.6vw);\n  font-family: var(--base-p-ff);\n  color: var(--text-color);\n}\n\n/* FOOTER */\n.footer {\n  position: fixed;\n  bottom: 8px;\n  left: 0;\n  width: 100vw;\n  display: flex;\n  justify-content: center;\n}\n\n.social-icons-container {\n  width: 35%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.footer-name {\n  color: var(--text-color);\n  font-size: calc(0.8rem + 0.7vw);\n  font-family: var(--base-p-ff);\n}\n\n.footer-icons-container {\n  width: 50%;\n  display: flex;\n  justify-content: space-evenly;\n}\n\n.github-img,\n.twitter-img {\n  width: max(35px, 15%);\n  cursor: pointer;\n}\n\n.github-img:hover,\n.twitter-img:hover {\n  scale: 1.1;\n}\n\n/* MEDIA QUERIES */\n@media (max-width: 800px) {\n  #general-weather-container {\n    height: 65%;\n    width: 80%;\n    display: grid;\n    grid-template-columns: 1.5fr 1fr;\n    background-color: var(--sub-base-color);\n    border-radius: 0.5em;\n  }\n}\n\n@media (max-width: 600px) {\n  #general-weather-container {\n    height: 65%;\n    width: 90%;\n    display: grid;\n    grid-template-columns: 1.5fr 1fr;\n    background-color: var(--sub-base-color);\n    border-radius: 0.5em;\n  }\n}\n\n@media (max-width: 500px) {\n  #general-weather-container {\n    height: 75%;\n    width: 100%;\n    display: grid;\n    grid-template-columns: 1.5fr 1fr;\n    background-color: var(--sub-base-color);\n    border-radius: 0.5em;\n  }\n  .location-prediction-container {\n    margin-top: 3%;\n    margin-bottom: 2%;\n    height: 35%;\n    width: 100%;\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    gap: 5px;\n  }\n\n  .prediction-container-1,\n  .prediction-container-2,\n  .prediction-container-3,\n  .prediction-container-4 {\n    width: 100%;\n  }\n\n  .right-weather-info {\n    width: 100%;\n  }\n\n  .celsius-icon,\n  .f-icon {\n    width: 20px;\n    filter: invert(88%) sepia(29%) saturate(142%) hue-rotate(350deg)\n      brightness(106%) contrast(88%);\n  }\n\n  .footer {\n    bottom: 20px;\n    width: 100%;\n  }\n\n  .social-icons-container {\n    width: 70%;\n  }\n\n  .loader {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100vh;\n    background-color: var(--base-color);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    z-index: 100;\n    transition:\n      opacity 0.75s,\n      visibility 0.75s;\n  }\n}\n"],